				background-size: 15.5px 12.5px;
			}
		}
	}

	// Attachment
	&.attachment {
		@apply relative;
		@apply xl:w-auto max-w-none;

		.post-thumbnail-wrap {
			@apply relative w-full h-full;
			@apply xl:w-auto max-w-none;
		}

		img {
			@apply absolute top-0 left-0 w-full h-full object-cover;
			@apply xl:w-auto max-w-none;
		}
	}

	// ORG Post Type
	&.type-post {
		@apply h-px503 border-teal-dark border-opacity-16 shadow-none border-px2;
		@apply md:min-h-px450;

		&.no-thumbnail {
			.title-top {
				@apply block font-normal mb-px20;
				@apply xl:mb-px24;
			}

			.post-desc {
				@apply block tracking-em_005;
				@apply md:text-px16 md:leading-px22;
			}
		}

		.card-text-container {
			@apply pt-px34 pb-px26;
		}

		.post-thumbnail-wrap {
			@apply -mt-px34 mb-px28 relative;
			padding-top: percentage(240/319);

			@screen md {
				@apply h-auto;
				padding-top: percentage(205/319);
			}

			img {
				@apply absolute top-0 left-0 h-full w-full;
			}
		}

		.title-top {
			@apply hidden;
		}

		.post-title {
			@apply text-px22 leading-px32 tracking-px_05;
			@apply xl:text-px26 xl:leading-px32 xl:tracking-em_005;
		}

		.post-desc {
			@apply hidden;
		}

		.tags-box {
			@apply flex items-center flex-wrap overflow-visible h-px30;

			// Hide 3rd tag item onwards
			&:not(.show-all) {
				.tag-box:nth-child(n + 3):not(.ellipsis) {
					@apply hidden;
				}
			}
		}

		.tag-box {
			@apply flex items-center h-px30 px-px14 mr-0 mb-0 bg-gray-light mr-px6 mb-px6 text-teal-dark;
			@apply xl:tracking-em001;

			&.ellipsis {
				@apply static w-px34 h-px34 flex items-center justify-center p-0 bg-blue_green mr-0 text-white;
			}
		}
	}

  // Icon
  .icon-wrap {
	@apply absolute top-6 right-5;
	font-size: 1.5rem;
  }

  body.is-rc & {
	@apply text-indigo;

	&.bg-full {
	  @apply text-white;
	}
  }

  @screen md {
	.post-title {
	  .title_top + & {
		@apply leading-px28;
	  }
	}

	.fundraiser-btm-wrap {
	  @apply text-px18 leading-px26;
	}

	.fundraiser-progress-wrap {
	  @apply h-px18;
	}
  }

  @screen lg {
	height: rem-calc(510px);

	.title-top,
	.title-btm {
	  @apply mb-4;
	}

	.post-title {
	  @apply text-px26 leading-px36 tracking-em005;

	  .title_top + & {
		@apply text-px26 leading-px36;
	  }

	  .bg-full & {
		@apply font-bold text-px40 leading-px48;
	  }
	}

	.is-rc & {
	  .post-title {
		@apply text-px22;
	  }
	}

	.fundraiser-progress-wrap {
	  @apply h-px20;
	}
  }

  @screen xl {
	.is-rc & {
	  .post-title {
		@apply text-px26;
	  }
	}
  }
}

